// button

.buttonContainer{



  margin-top: 50px;
  margin-bottom: 170px;
  margin-left: 0;


}





.button{
  cursor: pointer;
  height: 60px;
  width: 200px;
  display:block;

  position: relative;
  &:hover .button__frame{
    top:0;
    left:0;
    @include transition;
  }
  &:hover .button__colorBody{
    top:0;
    left:0;
    @include transition;
  }
  .button__colorBody{

    color: #fff;
    background: $marrakesh;
    z-index: 200;

    position: absolute;
    height: 100%;
    width: 90%;
    top:-5px;
    left:-5px;
    @include transition;



    display: flex;
    justify-content: center;
    align-items: center;

  span{
    text-transform: uppercase;

    letter-spacing: 1.4px;


  }


  }
  .button__frame{
    position: absolute;
    background: url(../img/rust.png) no-repeat bottom;
    background-size: cover;
    width: 90%;
    height: 100%;
    top:5px;
    left:5px;
    @include transition;
    z-index: 100;


  }
}



.general__wrapper{
    max-width: 1600px;
    padding: 0 20px;
    margin: auto;
    @include bp-s{
        padding: 0 40px;
    }
    @include bp-sm{
        padding: 0 50px;
    }
}

.general__wrapper--news{
    @include bp-m{


        max-width: 1600px;
        padding: 0 20px;
        margin: auto;


    }
}

