// ☄️about☄️
.about{
    background: url(../img/about.png) top no-repeat;
    background-size: cover;
    height: 100%;
}

.sectionTitleBlock{
    padding-top: 70px;


        display: flex;
        justify-content: flex-end;
        @include bp-xl{
            padding-top: 120px;
        }


}
.sectionTitleBlock__wrapper{
    position: relative;
    height: 300px;
    width: 300px;
    @include bp-l{
        height: 350px;
        width: 350px;
    }

    @include bp-xl{
        height: 400px;
        width: 400px;
    }
}

.sectionTitle{
    font-size: 4.5rem;
    letter-spacing: 0.1rem;
    font-weight: 400;
    color: #000;
    position: absolute;
    left: 15px;
    top:35px;
    z-index: 300;

    @include bp-l{
        left:45px;
        top:55px;
    }

    @include bp-xl{
        left:12px;
        top:15px;
        font-size: 5.5rem;
    }
}
.blueSquare{
    background-color: $electric;
    opacity: 0.6;
    height: 200px;
    width: 200px;
    position: absolute;
    @include bp-l{
        height: 230px;
        width: 230px;
    }

    @include bp-xl{
        height: 300px;
        width: 300px;
    }

    bottom: 0;
    right: 0;
}



.about__textBlock{
    padding-top: 120px;
    padding-bottom: 100px;
    @include bp-l{
        display: flex;
        flex-flow: column wrap;
        align-items: flex-start;
        padding-bottom: 120px;
        padding-top: 150px;

    }
}
.about__text{
    background-color: #000;
    color: #fff;
    font-size: 1rem;
    padding: 10px;
    @include bp-m{
        width: 45%;
    }


}

// ⭐️news⭐️

.news{
    background-color:$dust;

    background-image:url(../img/asfalt-dark.png);
    background-repeat: repeat;
    height:1500px;
    display: flex;
    justify-content: center;
    align-items: center;

    @include bp-s{
        max-height:1200px;
    }

    @include bp-sm{
        max-height:1400px;
    }

    @include bp-m{
        height:100vh;
    }

}

.news__wrapper{

    width: 100%;
    height: 80%;

    top:0%;
    display: flex;
    flex-flow: column nowrap;


    @include bp-m{
    position: relative;
    top:50%;
    transform: translateY(-50%);
    }


}
.text__wrapper{
    position: absolute;

    margin-left:20px;


     height: 70%;


    @include bp-m{
top:0;
    }


}
.news__headindsBlock{

  border-bottom: 13px solid transparentize($electric, 0.2);
  position: relative;
  width: 100%;
   z-index: 700;


  padding-bottom: 30px;

  margin-left: 20px;

  @include bp-s{
     margin-left: 40px;
  }
  @include bp-sm{
      margin-left: 50px;
  }

  @include bp-m{
      width: 55%;

  }
}
.news__textBlock{
  order:2;

    padding-top: 30px;
    width:100%;

    padding:  20px;

    @include bp-s{
        padding:40px;
    }
    @include bp-sm{
        padding: 40px 50px;
    }

    @include bp-m{
        width: 50%;

    }



}
.news__photo{

    margin-top: 50px;
    background: #fff;

    width: 100%;
    height:70%;


    @include bp-m{
    width: 50%;
    height: 80%;
    right:0;
    position: absolute;
    top:0;
    margin-top: 0;
    }


}

.news__title{
    font-size: 2rem;
    color: #000;
    padding-right: 50px;

    @include bp-xl{
        font-size: 2.5rem;
    }
}

