// 💿discography💿
.records{
    margin: 0 auto;
    background-image:url(../img/asfalt-dark.png);
    background-repeat: repeat;
    background-color: $dust;

}
.records__wrapper{
    display: flex;
    flex-flow: row wrap;
    padding: 70px 0;
    justify-content: space-between;
    align-items: center;




}


.records__singleRecord{
    width: 100%;
    display: block;
    display: flex;
    flex-flow: column nowrap;
    padding: 20px 0;
    justify-content: center;
    align-items: center;
    cursor: pointer;






    @include bp-m{

        width: 50%;


    }

    @include bp-xl{

        width: 33%;
        padding: 30px 0;
        justify-content: flex-end;


    }



}

.singleRecord__img{
    width: 220px;
    height:220px;

        box-shadow: 0px 1px 2px 0px rgba(0, 0, 0, 0.11);

        transition: 0.3s;

        &:hover{
          box-shadow: 5px 10px 10px -2px rgba(0, 0, 0, 0.19  );
        top:49%;
        }




    img{
        width: 100%;
    }

    @include bp-sm{
        width: 300px;
        height:300px;
    }


    @include bp-m{
        width: 300px;
        height:300px;

    }

    @include bp-l{
        width: 350px;
        height:350px;

    }
}
.singleRecord__title{
    text-transform: uppercase;
    color:#000;
    font-size:1rem;
    padding: 10px 0;
}















// 🛸tour🛸


.shows{
